/* @flow */
import { H2 } from 'src/components/styled/Heading';
import PageContainer from 'src/components/pages/PageContainer';
import PageDescription from 'src/components/styled/PageDescription';
import Panel from 'src/components/styled/Panel';
import React from 'react';

const EMAIL = 'matis.lepik@gmail.com';

export type Props = {};

export default class AboutPage extends React.Component {
  props: Props;

  render() {
    return (
      <PageContainer page="pantheons">
        <Panel>
          <PageDescription>
            <H2>About PoE checklist</H2>
            <p>Hello!</p>
            <p>
              I made this checklist to ease the league starting experience. Very
              often I find myself wondering if I had missed any passive points,
              or digging through the wiki to find which map I need for which
              pantheon upgrade, or buying all white maps at once to complete my
              atlas, but losing track of which one's I've already bought.
            </p>
            <p>
              Hopefully this site can help you with those things. If you check
              an item in one of the lists, it will be stored on your machine, so
              next time you come back, the information will still be here.
            </p>

            <p css={`margin-top: 25px`}>
              If you have any questions, contact me at{' '}
              <a href={EMAIL}>{EMAIL}</a>
            </p>
          </PageDescription>
        </Panel>
      </PageContainer>
    );
  }
}



